import React, { useContext, useState } from "react";
import { Button, Modal } from "react-bootstrap";
import TranslationContext from "../../contexts/Translation/TranslationContext";

const ImageModal = ({ img, title, text,path,url }) => {
  const translationContext = useContext(TranslationContext);
  const { language } = translationContext;
  
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <img style={{cursor:'pointer'}} onClick={handleShow} src={img} alt="" />
      <div className="mx-1">
        <p className="display-5" style={{ fontSize: 15, fontWeight: 400 }}>
          {title}
        </p>
      </div>

      <Modal show={show} size="lg" onHide={handleClose}>
        <Modal.Header>
          <Modal.Title>{title}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <img className="w-100" src={img} alt="" />
        </Modal.Body>
        <Modal.Footer>
          <p>{text}</p>
          <Button variant="outline-danger" onClick={handleClose}>
                  {language==="Español"?"Cerrar":"Close"}
            </Button>
          <a href={url} target='_blank' rel="noreferrer">
          <Button  variant="outline-secondary">
            {language==="Español"?"Ver en github":"See on github"}
            </Button>

          </a>
          <a href={path} target='_blank' rel="noreferrer">
           <Button variant="outline-primary">
               {language==="Español"?"Ver sitio web":"See website"}
           </Button>
          </a>

        </Modal.Footer>
      </Modal>
    </>
  );
};

export default ImageModal;
