import React, { useContext, useState } from "react";
import Color from "./Color/Color";
import TranslationContext from "../contexts/Translation/TranslationContext";

export default function Portada() {
   const translationContext = useContext(TranslationContext);
  const { language } = translationContext;
  const [color, setColor] = useState(null);
  const [colors, setColors] = useState([
    "#26de81",
    "#fc5c65",
    "#fd9644",
    "#3867d6",
    "#fffa65",
    "#ffffff",
  ]);
  const colores = [1, 2, 3, 4, 5, 6, 7, 8, 9];

  return (
    <div className="container">
      <div className="row r-container d-flex align-items-center">
        <div className="col-md-6">
          {language === "Español" && (
            <h1 className="h1">
              Rubén González, <br /> Desarrollador Full stack de JavaScript
            </h1>
          )}
          {language === "English" && (
            <h1 className="h1">
              Rubén González, <br /> Fullstack JavaScript developer
            </h1>
          )}

          {language === "Español" && (
            <p>
              A continuación, se mostrarán mis habilidades y proyectos realizados
            </p>
          )}
          {language === "English" && (
            <p>I will show you my skills and projects I have done below</p>
          )}
          {language === "Español" && (
            <a className="btn btn-primary me-2" href="#about">
              Sobre mí
            </a>
          )}
          {language === "English" && (
            <a className="btn btn-primary me-2" href="#about">
              About me
            </a>
          )}
        </div>
        <div className="cubo col-md-6 justify-content-center" id="cubo">
          {colores.map((el) => (
            <Color
              key={el}
              colors={colors}
              setColors={setColors}
              color={color}
              setColor={setColor}
            />
          ))}
        </div>
      </div>
    </div>
  );
}
