import React, { useContext } from "react";
import imagen1 from "../projects-pics/speedtimer.JPG";
import imagen2 from "../projects-pics/fotografia.JPG";
import imagen3 from "../projects-pics/translation.JPG";
import imagen4 from "../projects-pics/ChatApp.JPG";
import imagen5 from "../projects-pics/e-commerce.JPG";
import imagen6 from "../projects-pics/pm.jpg";

import Card2 from "./Card2";
import TranslationContext from "../contexts/Translation/TranslationContext";


const Proyectos = (props) => {
   const translationContext = useContext(TranslationContext);
  const { language } = translationContext;
  const cards = [
    {
      id: 1,
      title: language==="Español"?"Cronometro para cubos de Rubik":"Timer for Rubik's cube",
      image: imagen1,
      url: "https://github.com/ruben137/speedtimer",
      text: function () {
        switch (language) {
          case "Español":
            return "Uno de mis pasatiempos es armar cubos de rubik, así que, con HTML, CSS y JavaScript vanilla cree un cronometro para medir el tiempo";
          case "English":
            return "One of my hobbies is to solve Rubik's cubes, so with HTML, CSS and vanilla JavaScript I created a timer to measure time";
          default:
            break;
        }
      },

      path: "https://ruben137.github.io/speedtimer/",
    },
    {
      id: 2,
      title: language==="Español"?"Ejemplo de sitio web de fotografía":"Photography website example",
      image: imagen2,
      url: "https://github.com/ruben137/fotografia",
      text: function () {
        switch (language) {
          case "Español":
            return "Con HTML, CSS, bootstrap y JavaScript cree este sitio simple de fotografía, ideal para exponer tus trabajos y posicionar tu negocio en línea";
          case "English":
            return "With HTML, CSS, bootstrap and JavaScript I created this simple photography website, ideal to show your works and position your business online";

          default:
            break;
        }
      },
      path: "https://ruben137.github.io/fotografia/",
    },
    {
      id: 3,
      title:language==="Español"? "Traducir tu pagina web con react":"Translate your website with react",
      image: imagen3,
      url: "https://github.com/ruben137/webtranslation",
      text: function () {
        switch (language) {
          case "Español":
            return "En este pequeño proyecto con ayuda de React desarrollé una manera de agregar múltiples lenguajes a una página web de manera práctica y sencilla";
          case "English":
            return "In this small project with help of React I developed a way of adding multiples languages to a website in a practical and simple way";

          default:
            break;
        }
      },
      path: "https://ruben137.github.io/webtranslation/",
    },
    {
      id: 4,
      image: imagen4,
      title: "Chat app",
      url:"https://github.com/ruben137/chat-app",
      path:"https://rgprojects-chat-app.netlify.app/",
      text: function () {
        switch (language) {
          case "Español":
            return "Aplicación de chat en línea, cuenta con autenticación de google vía firebase y la funcionalidad del chat es implementada por la API chatengine.io";
          case "English":
            return "Web chat app, with Google Sign-in added with firebase and chatengine.io API to give the chat funcionality";

          default:
            break;
        }
      },
    },
    {
      id: 5,
      image: imagen5,
      title: language==="Español"?"E-commerce simple":"Simple e-commerce",
      path:"https://webcommerce-demo.netlify.app/",
      url:"https://github.com/ruben137/commerce.js",
      text: function () {
        switch (language) {
          case "Español":
            return "Proyecto de un e-commerce simple hecho con commerce.js API, cuenta con carrito de compras y pasarela de pago usando stripe";
          case "English":
            return "Simple e-commerce project made with commerce.js API, it has a shopping cart and stripe's payment gateway";

          default:
            break;
        }
      },
    },
    {
      id: 6,
      image: imagen6,
      path:"https://photo-media-react.netlify.app/",
      url:"https://github.com/ruben137/social-media",
      title: language==="Español"?"Red social hecha con el stack MERN":"MERN stack social media",
      text: function () {
        switch (language) {
          case "Español":
            return "Red social similar a instagram, hecha con el stack MERN y redux, cuenta con la funcionalidad de subir fotos, seguir usuarios, chatear con amigos y mucho mas!";
          case "English":
            return "Social media similar to instagram, made with the MERN stack and redux, it has features such as post pictures, follow users, chat with friends and much more!";

          default:
            break;
        }
      },
    },
  ];

  return (
    <div className="container">
      {language === "Español" && (
        <h3 className="h1 text-center display-5 p-4" id="proyectos">
          Mis proyectos
        </h3>
      )}
      {language === "English" && (
        <h3 className="h1 text-center display-5 p-4" id="proyectos">
          My projects
        </h3>
      )}
      <hr />
      <div className="container">
        <div className="row">
          {cards.map((card) => {
            return (
              <div className="col-md-4 my-2" key={card.id}>
                <Card2 {...card} />
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default Proyectos;
