import React, { useContext, useEffect } from "react";

import TranslationContext from "../contexts/Translation/TranslationContext"

const Navigation = () => {
  const translationContext = useContext(TranslationContext);
  const { language, setLanguage, getLanguage } = translationContext;

  useEffect(() => {
    getLanguage();
  }, []);


  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
      <div className="container">
        <a className="navbar-brand" href="/">
          RG projects
        </a>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav">
            <li className="nav-item">
              {language === "Español" && (
                <a className="nav-link" aria-current="page" href="#about">
                  Sobre mí
                </a>
              )}
              {language === "English" && (
                <a className="nav-link" aria-current="page" href="#about">
                  About me
                </a>
              )}
            </li>

            <li className="nav-item">
              {language === "Español" && (
                <a className="nav-link" href="#proyectos">
                  Proyectos
                </a>
              )}
              {language === "English" && (
                <a className="nav-link" href="#proyectos">
                  Projects
                </a>
              )}
            </li>
            <li className="nav-item">
              {language === "Español" && (
                <a className="nav-link" href="#tecnologias">
                  Tecnologías que uso
                </a>
              )}
              {language === "English" && (
                <a className="nav-link" href="#tecnologias">
                  Technologies that I use
                </a>
              )}
            </li>

          </ul>

          <ul className="navbar-nav ms-auto align-items-md-center">

            <li className="nav-item">
              <select
                className="form-select py-0"
                aria-label="Default select example"
                onChange={(e) => {
                  setLanguage(e.target.value);
                  localStorage.setItem(
                    "language",
                    JSON.stringify(e.target.value)
                  );
                }}
                name="language"
              >
                <option selected={language === "Español"}>Español</option>
                <option selected={language === "English"}>English</option>
              </select>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default Navigation;
