import React, { useContext } from "react";
import Skill from './Skill/Skill'
import img1 from "../img/html.png";
import img2 from "../img/css.png";
import img3 from "../img/javascript.png";
import img4 from "../img/bootstrap.png";
import img5 from "../img/nodejs.png";
import img6 from "../img/react.png";
import img7 from "../img/git.png";
import img8 from "../img/material-ui.png";
import img9 from "../img/mongo.png";
import img10 from "../img/firebase.png";
import TranslationContext from "../contexts/Translation/TranslationContext";

function Cards() {
   const translationContext = useContext(TranslationContext);
  const { language } = translationContext;
  const cards = [
    {
      title: "HTML",
      image: img1,
    },
    {
      title: "CSS",
      image: img2,
    },
    {
      title: "Javascript",
      image: img3,
    },
    {
      title: "Bootstrap",
      image: img4,
    },
    {
      title: "Node.js",
      image: img5,
    },
    {
      title: "React",
      image: img6,
    },

    {
      title:"Git & Github",
      image: img7,
    },
    {
      title:"Material-UI",
      image: img8,
    },
    {
      title:"MongoDB",
      image: img9,
    },
    {
      title:"Firebase",
      image: img10,
    },
  ];


  return (
    <>
 
      {language === "Español" && (
        <h3
          className="text-center p-4 h1 display-5"
          id="tecnologias"
        >
          Tecnologías
        </h3>
      )}
      {language === "English" && (
        <h3
          className="text-center p-4 h1 display-5"
          id="tecnologias"
        >
          Skillset
        </h3>
      )}
 <hr />
     
     <div className="container">
        <div className="row">
          {cards.map((card, index) => (
            <Skill card={card} index={index}/>
          ))}
      </div>
     </div>
  


  
    </>
  );
}

export default Cards;
