import React, { useContext } from "react";
import TranslationContext from "../contexts/Translation/TranslationContext";
import imagen from "../img/yo.png";

export default function About() {
   const translationContext = useContext(TranslationContext);
  const { language } = translationContext;
  return (
    <div className="contenedor" id="about">
      <div className="container text-center">
        <div className="row align-items-center">
          <div className="col-md-6 text-start mt-5">
            {language === "Español" && <h3 className="h1" style={{color:'black'}}>Sobre mí</h3>}
            {language === "English" && <h3 className="h1" style={{color:'black'}}>About me</h3>}
            {language === "Español" && (
              <p className="text-start">
               Mi nombre es Rubén González, tengo 21 años, soy de Venezuela y soy un apasionado del desarrollo web, soy autodidacta y tengo la habilidad de aprender las cosas rápido, me especializo en las tecnologías relacionadas con JavaScript, en cuanto al frontend domino la librería React y para desarrollar el backend uso nodejs y express, pero de igual manera tengo conocimiento de APIs externas tales como firebase, en este portafolio hay muestra de lo que puedo hacer con mis habilidades, y día a día me esfuerzo por mejorar y aprender nuevas cosas.
              </p>
            )}
            {language === "English" && (
              <p className="text-start">
                My name is Rubén González, I'm 21 years old, I'm from Venezuela
                and I'm passionate about web development, I'm a self taught person and I learn things fast, I use javascript technologies, React in the frontend and nodejs and express in the backend, but I also have knowledge in external APIs sucha as firebase, in this portfolio I show a part of what I'm able to do with my current skills, and everyday I study to improve myself.
              </p>
            )}
            {language === "Español" && (
              <a className="btn btn-secondary" href="#contacto">
                Contáctame
              </a>
            )}
            {language === "English" && (
              <a className="btn btn-secondary" href="#contacto">
                Contact me
              </a>
            )}
          </div>

          <div className="col-md-6">
            <img className="imagen" src={imagen} alt="" />
          </div>
        </div>
      </div>
    </div>
  );
}
