import React, { useState } from "react";

const Skill = ({ card, index }) => {
  const [show, setShow] = useState(false);
  const bgColor = (index) => {
    if (index === 0) return "#fa8231";
    if (index === 1) return "#3867d6";
    if (index === 2) return "#fff200";
    if (index === 3) return "#8854d0";
    if (index === 4) return "#3d3d3d";
    if (index === 5) return "#778ca3";
    if (index === 6) return "#d1ccc0";
    if (index === 7) return "#2e5ac0";
    if (index === 8) return "#20190e";
    if (index === 9) return "#4b7bec";
  };

  return (
    <div
      className="col-xs-4 col-md-6"
      style={{ maxWidth: 210, position: "relative", margin: "12px 0" }}
      onMouseOver={() => setShow(true)}
      onMouseOut={() => setShow(false)}
    >
      {show && (
        <div
          style={{
            position: "absolute",
            top: -40,
            background: "#2a2a2a",
            borderRadius: 5,
            color: "#fff",
            padding: 5,
          }}
        >
          <span style={{ fontWeight: 600 }}>{card.title}</span>
          <div
            style={{
              background: "#2a2a2a",
              position: "absolute",
              left: 0,
              right: 0,
              margin: "auto",
              width: 8,
              height: 8,
              transform: "rotate(45deg)",
            }}
          ></div>
        </div>
      )}
      <img
        className="p-1"
        style={{
          width:'100%',
          backgroundColor: bgColor(index),
          borderRadius: "13%",
        }}
        src={card.image}
        alt=""
      />
    </div>
  );
};

export default Skill;

